.App {
    height: 100vh;
}

.Cart {
    position: relative;

}
.Cart div {
    height: 100%;
    overflow-y: scroll;
}
.Cart table {
    width: 100%;
    margin-bottom: 55px;
}
.Cart table thead {
    font-weight: bold;
    position: sticky;
    top: 0;
    background-color: white;
}
.Cart table thead td {
    padding-top: 0;
}
.Cart table tr {
    cursor: pointer;
}
.Cart table tr:nth-child(even) {
    background: lightgreen;
}
.Cart table td.quantity {
    text-align: right;
}
.Cart table td.price, .Cart div.sum.price {
    font-variant-numeric: tabular-nums;
    text-align: right;
}
.Cart div.sum.price {
    height: 18px;
    padding-right: 2px;
    font-weight: bold;
    border-top: black 1px solid;
    position: absolute;
    overflow: visible;
    right: 0;
    bottom: 0;
    width: 100%;
    background-color: white;
}
.Cart div.sum.price button {
    position: absolute;
    left: 5px;
    bottom: 5px;
    height: 3rem;
    width: 3rem;
    background: red;
}

.Controls {
    scroll-snap-type: x mandatory;
    display: flex;
    -webkit-overflow-scrolling: touch;
    overflow-x: scroll;
    overflow-y: hidden;
}
.Controls > div {
    height: 100%;
    min-width: 100vw;
    scroll-snap-align: start;
}

.head {
    width: 100%;
    padding-top: 3px !important;
    padding-bottom: 3px !important;
    font-weight: bold;
    text-align: center;
    background-color: lightgray;
}

.Products {
    height: calc(100% - 24px);
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 0 0;
}
.Products button {
    font-size: 1.0em;
    padding: 0;
}
.Products button .name {
    font-weight: bold;
    word-break: break-word;
    min-height: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Products button .amount {
    font-size: 0.7em;
}
.Products button.soft {
    background-color: green;
    color: white !important;
}
.Products button.medium {
    background-color: yellow;
}
.Products button.hard {
    background-color: red;
}


@media (orientation: landscape) {
    .App {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0 0;
    }
    .Controls {
        scroll-snap-type: y mandatory;
        display: flex;
        flex-direction: column;
        overflow-x: hidden;
        overflow-y: scroll;
    }
    .Controls > div {
        height: 100vh;
        min-width: 50vw;
    }
}

@media (orientation: portrait) {
    .Cart {
        height: 50vh;
    }
    .Controls {
        height: 50vh;
    }
}
